.product {
  cursor: pointer;

  -webkit-box-shadow: 5px 5px 5px 0px rgba(217, 217, 219, 1);
  -moz-box-shadow: 5px 5px 5px 0px rgba(217, 217, 219, 1);
  box-shadow: 5px 5px 5px 0px rgba(217, 217, 219, 1);
  .img {
    padding-top: 20px;
  }
  .information {
    border-top: 1px solid #d9d9db;
    margin-top: 10px;
    padding-top: 10px;
  }
}
.product:hover {
}
.text-link {
  color: inherit;
}
.text-link:hover {
  text-decoration: inherit;
}
